<script>
import dayjs from 'dayjs';
import { MessageBox, Toast } from 'mint-ui';
import { Button } from 'vant';
import { isRole } from '@/utils/filters';
import {GetCurrentRoomRefund,PostReFundAgain} from '@/api/api_refund';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessCooperOrder, GetBusinessCooperRenewalContractByCooperOrder, GetBusinessRoomFreeCooperByhotel } from '@/api/api_business';
import {
    GetBusinessRenewalContractByOrderRoom,
    PostBusinessOrderroomSendMessageAgain,
    PostBusinessOrderroomLeave,
    PostBusinessOrderroomPasswordSend,
    PostBusinessOrderroomCancel,
    PostBusinessOrderroomuserRemove,
    PostBusinessOrderroomDelaySwitchOn,
    PostBusinessOrderroomAutoDetainOn,
    PostBusinessOrderroomAutoDetainOff,
    PostBusinessOrderroomNocardOn,
    GetBusinessOrderroomuserByorderroom,
    GetBusinessDepositCutRecordByorderroom,
    GetBusinessOrderroom,
    PostBusinessOrderroomRoomchange,
    PostBusinessUserCouponAdd,
    PostBusinessOrderroomUserM,
    PostBusinessOrderRoomContinue
} from "@/api/api_business_hotel_and_scale";
import { dataFilter, nameFilter } from '../../utils/filters';
import { Dialog } from 'vant';


export default {
    data() {
        return {
            // refundStatus == 0 退款失败时，可申请重新退款
            //refundStatus: 0 退款失败  1 退款中 2  退款成功 -1 初始化,
            refundStatus:-1,
            refundError:'',
            refundId: -1,
            status: [1, 2],
            payment: '',
            order_add: {
                1: '哎哟有房',
                2: '订单同步',
                3: '手动分房',
                4: '短信接单'
            },
            c_unix: '',
            value: '',
            pass_phone: '',
            currentRole: '',
            number: '',
            dictLabel: '',
            currentInfo: null,
            getLocalInfo: null,
            roomPayments: null,
            businessOrderAddSource: null,
            conInfo: null,
            orderPayMent: {},
            discountInfo: '',
            depositStatu: '',
            roomStatus: [],
            cutRecordInfo: [],
            infoList: [],
            payments: [],
            signingInfo: [],
            patmentObj: [],
            priceRecordList: [],
            roles: [],
            cooperChangeRooms: [],
            paymentList: [],
            orderSourceMap: [],
            ordertTypeAll: [],
            selectedData: [],
            coupons: [],
            priceRecordListBackInfo: [],
            conOrderRoomDtosArr: [],
            allInfo: [],
            editPensonForm: {
                roomUser: null,
                phone: null,
                orderId: null,
                orderRoomId: null
            },
            conOrderRoomDtosArr_totlePrice: null,
            d_commission: null,
            next_day: null,
            orderStatusAll: null,
            reducePrice: null,
            show: false,
            on: false,
            disabled: false,
            disabled1: false,
            disabled2: false,
            showleave: false, //提前退房
            sheetVisible: false,
            isLock: false,
            edit_box: false,
            person_box: false,
            deposit: false,
            password_show: false,
            turnOpen: false,
            Signing: false,
            renting: false,
            cooperRooms: false,
            showCoupon: false,
            showTime: false,
            is_roleCooperManager: false,
            is_roleManager: false,
            conOrder_show: false,
            show_conAll: false,
            unixTime: false, // 判断-是否能取消续住-展示
            btn_back_deposit: false,
            show_editPerson: false,
            memName: '',
            memNumber: '',
            menPrice: '',
            date: '选择有效时间段',
            delayValue: '',
            continueDay: 0, // 续住天数
            showContinueDialog: false, // 是否展示续住弹框,设置续住天数;
            showContinue: false // 是否展示手动续住; (前置条件：已入住,未续住,日租房)
        };
    },
    async created() {
        this.$store.commit('setHeader', '订单详情');
        this.orderRoomId = this.$route.query.orderRoomId;
        await this.getUnitStatu({ dictType: 'business_payment_method' }, 'roomPayments');
        if (this.$route.query.orderRoomId) {
            await this.getAddSource({ dictType: 'business_order_add_source' }, 'businessOrderAddSource');
            await this.getUnitStatu({ dictType: 'business_orderroom_status' }, 'roomStatus');
            this.getCurrentInfo();
            this.getRefundStatus();
        } else {
            await this.getUnitStatu({ dictType: 'business_cooper_order_status' }, 'roomStatus');
            this.getCooperCurrentInfo(this.$route.query.cooperOrderId);
        }
        this.is_roleCooperManager = isRole('ROLE_COOPER_MANAGER');
        this.is_roleManager = isRole('ROLE_MANAGER');
        this.c_unix = dayjs().unix();
    },
    computed: {
        getStatus: {
            get() {
                if (this.currentInfo) {
                    return this.roomStatus.find((item) => item.dictValue == this.currentInfo.status).dictLabel;
                }
            }
        },
        getPayments: {
            get() {
                if (this.currentInfo) {
                    return this.roomPayments.find((item) => {
                        return item.dictValue == this.currentInfo.paymentMethod || item.dictValue == this.currentInfo.order.paymentMethod;
                    }).dictLabel;
                }
            }
        }
    },
    methods: {
      nameFilter,
      dataFilter,
        // 拨号
        callPhone(phoneNumber) {
            window.location.href = 'tel://' + phoneNumber;
        },
        // 根据id获取单位订单
        getCooperCurrentInfo(id) {
            GetBusinessCooperOrder({ cooperOrderId: id }).done((res) => {
                this.getCooperSigning(id);
                this.currentInfo = res.data;
            });
        },
        async getAddSource(obj, data) {
            await GetSystemDictDataByType(obj).done((res) => {
                let new_obj = {};
                res.data.map((item) => {
                    new_obj[item.dictValue] = item.dictLabel;
                });
                this[data] = new_obj;
            });
        },
        async getUnitStatu(obj, data) {
            await GetSystemDictDataByType(obj).done((res) => {
                this[data] = res.data;
            });
        },
        // 根据单位订单查询合同
        getCooperSigning(id) {
