                this.firstGo();
                let list = res.data.map((item) => {
                    this.$set(item, 'setStatu', false);
                    return item;
                });
                this.signingInfo = list;
            });
        },
        // 时间排序
        sortKey(array, key) {
            return array.sort(function (a, b) {
                var x = a[key];
                var y = b[key];
                return x > y ? -1 : x < y ? 1 : 0;
            });
        },
        getRefundStatus() {
            GetCurrentRoomRefund(this.orderRoomId).done((res) => {
                  console.log('GetCurrentRoomRefund 结果:',res);
                   const  tempRes = res.data;
                  if (tempRes && tempRes != undefined && tempRes != null) {
                    this.refundStatus = tempRes.status;
                    this.refundId = tempRes.id;
                    this.refundError = tempRes.description;
                  }
            });
        },
        handleContinueFn() {
          if (this.continueDay == 0) {
              // Dialog.alert({
              //     title: '提示',
              //     message: '续住天数不能为0',
              //     showConfirmButton: false,
              //     confirmButtonText: '取消'
              // }).then(() => {
              //     // on close
              // });
              Toast({
                  message: '续住天数不能为0',
                  duration: 1500,
              });
              return;
          }
          const params = {
              orderRoomId: this.orderRoomId,
              continueDay: this.continueDay,
              orderId: this.currentInfo.orderId,
          };
          PostBusinessOrderRoomContinue(params).done((res) => {
              Toast({
                  message: '续住成功',
                  duration: 1500,
              });
          });
        },
         // 退款详情
        handleRefund() {
          MessageBox.confirm('', {
            message: '确认要对当前订单发起重新退款？',
            title: '重新退款',
            cancelButtonText: '取消',
            confirmButtonText: '确定'
          })
            .then((action) => {
              if (action == 'confirm') {
                PostReFundAgain({refundRecordId: this.refundId}).done((res) => {
                  console.log("重新退款结果: ", res);
                  Toast({
                    message: '退款成功',
                    duration: 1000
                  });
                  this.getRefundStatus();
                })
              }
            })
            .catch((e) => {
              console.log(e);
            });

        },
        // 当前信息
        getCurrentInfo() {

            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                this.ordertTypeAll = res.data;
            });
            GetBusinessOrderroom(this.orderRoomId).done((res) => {
                let arr;
              localStorage.setItem('moreInfo', JSON.stringify(res.data));
              console.log('当前信息 => currentInfo:', res.data);
                this.currentInfo = res.data;
                this.getOrderType('status', 'business_payment');
                this.firstGo();
                this.next_day = dayjs(dayjs(this.currentInfo.order.startTime).add(1, 'day').format('YYYY-MM-DD') + ' ' + '08:00:00').unix();

                // 前置条件：已入住,未续住,日租房 。
                // 满足前提条件可以手动续住
                if (
                    (res.data.status == '2' || res.data.status == 2) &&
                    res.data.isCon == 0 &&
                    res.data.orderType == '1') {
                    this.showContinue = true;
                } else {
                    this.showContinue = false;
                }

                if (this.currentInfo.priceRecord) {
                    let a = JSON.parse(this.currentInfo.priceRecord);
                  arr = [];
                  for (let key in a) {
                        //取键
                        let o = {};
                        o.time = key;
                        o.price = Number(a[key]);
                        arr.push(o);
                    }
                    this.sortKey(arr, 'time');
                    this.priceRecordList = arr;
                }
                if (this.currentInfo.dayIn && this.currentInfo.priceRecord) {
                    let a = JSON.parse(this.currentInfo.priceRecord);
                    arr = [];
                    for (let key in a) {
                        //取键
                        let o = {};
                        o.time = key;
                        o.price = Number(a[key]);
                        arr.push(o);
                    }
                    this.sortKey(arr, 'time');
                    this.priceRecordListBackInfo = arr;
                    this.priceRecordListBackInfo.reverse().splice(0, this.currentInfo.dayIn);
                }
                this.currentInfo.order.payment > 0 ? '已支付' : '未支付';
                let c_time = dayjs().unix();
                if (this.currentInfo.conOrderRoomDtos && this.currentInfo.conOrderRoomDtos.length > 0) {
                    // console.log('-----------------------------续住');
                    this.currentInfo.conOrderRoomDtos.map((item) => {
                        let dayIn = item.dayIn || 0;
                        let diffDay = item.diffDay || 0;
                        let d_diffDay = 1 - diffDay - dayIn;
                        let d_day = dayjs(dayjs(item.endTime).add(Math.abs(d_diffDay), 'day')).format('YYYY-MM-DD') + ' ' + '08:00:00';
                        let d_day_cuo = dayjs(d_day).unix();
                        if (item.status == '1' && item.status == '2') {
                            item.btn_back_deposit = true;
                        } else {
                            if (d_day_cuo > c_time) {
                                item.btn_back_deposit = true;
                            } else {
                                item.btn_back_deposit = true;
                            }
                        }
                        let con_arr = [];
                        let totlePrice = null;
                        if (JSON.parse(item.priceRecord)) {
                            for (const k in JSON.parse(item.priceRecord)) {
                                let con_obj = {};
                                con_obj.time = k;
                                con_obj.price = JSON.parse(item.priceRecord)[k];
                                totlePrice += Number(JSON.parse(item.priceRecord)[k]);
                                con_arr.push(con_obj);
                            }
                        }
                        this.sortKey(con_arr, 'time');
                        item.con_arr = con_arr.reverse();
                        item.totlePrice = totlePrice;
                        if (item.payment > '0' && item.payment < '5') {
                            // 有佣金
                            item.d_roomPrice = item.roomRealAmount * (1 - item.commission);
                        } else {
                            item.d_roomPrice = item.roomRealAmount;
                        }
                        this.conOrderRoomDtosArr_totlePrice += item.d_roomPrice;
                    });
                    let startT = dayjs(dayjs(dayjs(this.currentInfo.conOrderRoomDtos[this.currentInfo.conOrderRoomDtos.length - 1].startTime).format('YYYY-MM-DD') + ' ' + '08:00:00').add(1, 'day')).unix();
                    let cTime = dayjs().unix();
                    if (cTime < startT) {
                        this.unixTime = true;
                    }
                    this.conOrderRoomDtosArr = this.currentInfo.conOrderRoomDtos.reverse();
                } else {
                    if (this.currentInfo.status == '4' || this.currentInfo.status == '7') {
                        if (this.currentInfo.orderType == '1') {
                            let dayIn = this.currentInfo.dayIn || 0;
                            let diffDay = this.currentInfo.order.diffDay || 0;
                            let d_diffDay = 1 - diffDay + dayIn;
                            let d_day = dayjs(dayjs(this.currentInfo.order.endTime).add(Math.abs(d_diffDay), 'day')).format('YYYY-MM-DD') + ' ' + '08:00:00';
                            let d_day_cuo = dayjs(d_day).unix();
                            if (d_day_cuo > c_time) {
                                this.btn_back_deposit = true;
                            }
                        } else if (this.currentInfo.orderType == '2') {
                            let d_hour = dayjs(dayjs(this.currentInfo.leaveTime).add(2, 'day')).format('YYYY-MM-DD') + ' ' + '08:00:00';
                            let d_hour_cuo = dayjs(d_hour).unix();
                            if (d_hour_cuo > c_time) {
                                this.btn_back_deposit = true;
                            }
                        }
                    } else if (this.currentInfo.status == '1' || this.currentInfo.status == '2') {
                        this.btn_back_deposit = true;
                    }
                    this.commission = this.currentInfo.order.commission; //  佣金
                    // console.log('---------------------非续住');
                    // 有佣金
                    if ((this.currentInfo.order.payment > '0' && this.currentInfo.order.payment < '5') || this.currentInfo.order.depositPayment == '12') {
                        this.d_commission = this.currentInfo.roomRealAmount * (1 - this.commission);
                    } else {
                        this.d_commission = this.currentInfo.roomRealAmount;
                    }
                }
                if (this.currentInfo.depositCut > 0) {
                    this.getDepositCutReason();
                }
                if (this.currentInfo.hotel.faceApi == 0) {
                    this.isLock = false;
                } else {
                    if (this.currentInfo.isNoCard == 1) {
                        this.isLock = false;
                    } else {
                        this.isLock = true;
                    }
                }
                if (this.currentInfo.order.phone) {
                    this.number = this.currentInfo.order.phone;
                    if (this.number) {
                        this.Local(this.number);
                    }
                } else if (this.currentInfo.sysUser) {
                    this.number = this.currentInfo.sysUser.phone ? this.currentInfo.sysUser.phone : '';
                    if (this.number) {
                        this.Local(this.number);
                    }
                }
                // 延时退房
                if (this.currentInfo.isAutoDetain == 1) {
                    this.value = true;
                } else {
                    this.value = false;
                }
                // 免费延时至14:00
                if (this.currentInfo.order.endTime.substr(11, 8) == '14:00:00') {
                    this.delayValue = true;
                } else {
                    this.delayValue = false;
                }
                this.liverInfo();
            });
        },
