        // 延时退房
        openedDelay() {
            let endT = dayjs(this.currentInfo.order.endTime).unix();
            let isEndT = dayjs(dayjs().format('YYYY-MM-DD HH:mm:ss')).unix();
            if (isEndT < endT) {
                if (this.value == true) {
                    MessageBox.confirm('', {
                        message: '确认关闭延时退房？',
                        title: '延时退房',
                        cancelButtonText: '取消',
                        confirmButtonText: '确定'
                    })
                        .then((action) => {
                            if (action == 'confirm') {
                                PostBusinessOrderroomAutoDetainOff(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                                    this.value = false;
                                });
                            }
                        })
                        .catch((e) => {
                            console.log(e);
                        });
                } else {
                    MessageBox.confirm('', {
                        message: '确认开启延时退房？',
                        title: '延时退房',
                        cancelButtonText: '取消',
                        confirmButtonText: '确定'
                    })
                        .then((action) => {
                            if (action == 'confirm') {
                                PostBusinessOrderroomAutoDetainOn(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                                    this.value = true;
                                });
                            }
                        })
                        .catch((e) => {
                            console.log(e);
                        });
                }
            } else if (isEndT > endT) {
                MessageBox.confirm('', {
                    message: '已开始计费，无法关闭延时退房，可以直接退房',
                    title: '提示',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                });
            }
