                return false;
            }
            if (!this.memNumber) {
                Toast({
                    message: '请输入数量',
                    duration: 900
                });
                return false;
            }
            if (!this.menPrice) {
                Toast({
                    message: '请输入满减金额',
                    duration: 900
                });
                return false;
            }
            if (!this.startTime) {
                Toast({
                    message: '请选择有效时间段',
                    duration: 900
                });
                return false;
            }
            if (!this.orderType) {
                Toast({
                    message: '请选择领取范围',
                    duration: 900
                });
                return false;
            }
            let params = {
                couponSource: '2',
                userId: this.currentInfo.userId,
                hotelId: this.currentInfo.hotelId,
                couponAmount: this.memName,
                fullAmount: this.menPrice,
                couponNum: this.memNumber,
                startTime: this.startTime,
                endTime: this.endTime,
                orderType: this.orderType
            };
            PostBusinessUserCouponAdd(params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            this.showCoupon = false;
