        },
        // 重新下发短信-确定
        pass_ok() {
            // 已验证191,181手机号
            if (!this.pass_phone || !/^1[3-9]\d{9}$/.test(this.pass_phone)) {
                Toast({
                    message: '请正确输入手机号',
                    duration: 900
                });
                this.pass_phone = '';
                return false;
            }
            let roomUser = this.currentInfo.sysUser == null ? this.currentInfo.order.roomUser : this.currentInfo.sysUser.realName;
            let data = {
                roomUser: roomUser,
                phone: this.pass_phone,
                orderId: this.currentInfo.orderId
            };
            PostBusinessOrderroomSendMessageAgain(this.currentInfo.id, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            this.password_show = false;
        },
        // 换表
        changeWatch() {
            this.$router.push({
                path: '/changeWatch',
                query: {
                    id: this.currentInfo.id,
                    orderId: this.currentInfo.orderId
                }
            });
        },
        // 电费充值
        eleCharge() {
            if (this.currentInfo.status == 4 || this.currentInfo.status == 7) {
                this.$router.push({
                    path: '/eleRecord',
                    query: {
                        id: this.currentInfo.id
                    }
                });
            } else {
                this.$router.push({
                    path: '/eleManage',
                    query: {
                        hotelName: this.currentInfo.hotel.hotelName,
                        scaleName: this.currentInfo.roomScale.scaleName,
                        virscaleName: this.currentInfo.virtualRoomScale.scaleName,
                        roomSn: this.currentInfo.roomSn,
                        orderRoomId: this.currentInfo.id,
                        orderId: this.currentInfo.orderId
                    }
                });
            }
        },
        // 月租房费账单
        btnRoomFee() {
            this.$router.push({
                path: '/roomFee',
                query: {
                    id: this.currentInfo.id,
                    orderId: this.currentInfo.orderId
                }
            });
        },
        // 月租生活账单
        btnLifeFee() {
            this.$router.push({
                path: '/lifeFee',
                query: {
                    id: this.currentInfo.id
                }
            });
        },
        // 签约展开与折叠
        isSigning() {
            this.Signing = !this.Signing;
        },
        // 签约合同展开与折叠
        isRent(item) {
            item.setStatu = !item.setStatu;
        },
        // 根据订单房间查询合同
        getSigning() {
            GetBusinessRenewalContractByOrderRoom(this.currentInfo.id).done((res) => {
                let list = res.data.map((item) => {
                    this.$set(item, 'setStatu', false);
                    return item;
                });
                this.signingInfo = list;
            });
        },
        // cooper
        btnchange_cooper() {
            GetBusinessRoomFreeCooperByhotel({ hotelId: this.currentInfo.hotelId }).done((res) => {
                this.cooperChangeRooms = res.data;
                this.cooperRooms = true;
            });
        },
        choosedRoomcancle() {
            this.cooperRooms = false;
        },
        choosedRoom(item) {
            let params = {
                orderId: this.currentInfo.orderId,
                toScaleId: this.currentInfo.roomScaleId,
                toVirtualRoomScaleId: '0',
                toRoomId: item.id
            };
            PostBusinessOrderroomRoomchange(this.currentInfo.id, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(0)', 900);
            });
        },
        // 开票
        goRoute(link) {
            this.$router.push({
                path: link,
                query: {
                    orderRoomId: this.$route.query.orderRoomId,
                    invoiceAmount: this.currentInfo.invoiceAmount
                }
            });
        },
        goDepositCut() {
            this.$router.push({
                path: '/depositCut-page',
                query: {
                    isOpen: this.conOrderRoomDtosArr.length > 0 ? info.btn_back_deposit : this.btn_back_deposit,
                    orderId: this.currentInfo.orderId,
                    orderRoomId: this.currentInfo.id,
                    depositReverseAmount: this.currentInfo.depositReverseAmount,
                    data: this.cutRecordInfo
                }
            });
        },
        // 汇总
        goNext(url, type, info) {
            if (this.conOrderRoomDtosArr.length > 0) {
                // 续住
                info.deposit = this.currentInfo.deposit;
                info.couponAmount = info.userCoupon;
                info.roomScaleDiscountId = this.currentInfo.order.roomScaleDiscountId; // 自定义传
                info.depositPayment = this.currentInfo.order.depositPayment; // 自定义传
                info.refuseLandLord = info.refuseLandLord;
                info.reverseAmount = info.reverseAmount;
                info.leaveTime = this.currentInfo.leaveTime;
                info.orderType = this.currentInfo.orderType;
                info.commission = info.commission;
                info.roomRefuseAmount = info.roomRefuseAmount;
                info.payTime = info.payTime;
                info.payment = info.payment;
                info.roomPrice = info.roomPrice;
                info.memberDiscount = info.memberDiscount;
                info.roomScaleDiscount = info.roomScaleDiscount;
                localStorage.setItem('payDetails', JSON.stringify(info));
            } else {
                // 非续住
                localStorage.setItem('payDetails', JSON.stringify(info));
            }
            this.$router.push({
                path: url,
                query: {
                    isOpen: this.conOrderRoomDtosArr.length > 0 ? info.btn_back_deposit : this.btn_back_deposit,
                    type: type,
                    orderType: this.currentInfo.orderType
                }
            });
        },
        // 展开房费收入---All
        openAll(allInfo) {
            if (this.currentInfo.orderType == '2') {
                this.priceRecordList = [];
                let obj = {};
                obj.time = this.currentInfo.order.startTime;
                obj.diffDay = this.currentInfo.order.diffDay;
                obj.price = Number(this.currentInfo.roomPrice - this.currentInfo.deposit).toFixed(2) || '0';
                this.priceRecordList.push(obj);
                this.allInfo = this.priceRecordList;
            } else {
                this.reducePrice = 0;
                this.allInfo = allInfo;
                this.allInfo.map((item) => {
                    this.reducePrice += Number(item.d_roomPrice);
                });
            }
            this.show_conAll = true;
        },
        // 取消续住
        cancelIncon(item) {
            MessageBox.confirm('', {
                message: '确定取消续住订单？',
                title: '取消续住订单',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessOrderroomCancel(item.id, { orderId: item.orderId }).done((res) => {
                            this.conOrderRoomDtosArr = [];
                            this.getCurrentInfo();
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 修改联系人
        editPersonConfirm(action, done) {
            this.editPensonForm.orderRoomId = this.currentInfo.id;
            this.editPensonForm.orderId = this.currentInfo.orderId;
            if (action == 'confirm') {
                done(false);
                if (!this.editPensonForm.roomUser) {
                    Toast({
                        message: '请填写入住姓名',
                        duration: 900
                    });
                    return;
                }
                // 已验证191,181手机号
                if (this.editPensonForm.phone && !/^[1][3,4,5,7,8,9][0-9]{9}$/.test(this.editPensonForm.phone)) {
                    Toast({
                        message: '手机号码有误',
                        duration: 900
                    });
                    this.editPensonForm.phone = '';
                    return;
                }
                PostBusinessOrderroomUserM(this.editPensonForm).done((res) => {
                    Toast(res.msg);
                    this.getCurrentInfo();
                });
                this.show_editPerson = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_editPerson = false;
            }
        },
        goNextPage(url, item) {
            if (item.roomId > 0 && item.order.rule && JSON.parse(item.order.rule) && JSON.parse(item.order.rule).parkRule.park == 1) {
                this.$router.push({
                    path: url,
                    query: {
                        orderRoomId: this.$route.query.orderRoomId,
                        carNo: item.carNo
                    }
                });
            } else {
                Toast('123');
            }
        }
    }
};
</script>
